    },
    productTabChange(event) {
      //console.log(event.target.value)
      if(event.target.value =="Db"){
        EventBus.$emit("dbPaginationInfo",this.dbPagination);
      }else if(event.target.value =="Dome"){
        EventBus.$emit("domePaginationInfo",this.domePagination);
      }
    },
    storageInit : function(){
        this.dome_check=[];
        this.db_check=[];
    },
    //상품 등록
    storageRegister : function(){
      if(this.selectedShop == "Dome"){
        if(this.dome_check == null || this.dome_check == ""){
          alert("상품을 선택해주세요.");
        }else{
          axios.post('/api/product-manage/v1/storage-product',this.domeConvert(this.dome_check)).then(res => {
            alert("등록 완료");
          }).catch(res => {
            if(res.response.status =="401" || res.response.status =="302"){
              alert("인증 기간이 만료되었습니다.");
              this.$router.go('/');
            }else{
              this.modalMessage=res.response.data;
              this.isModalViewed=true;
            }
          })
        }
      } else if(this.selectedShop == "Db"){
        if(this.db_check == null || this.db_check === ""){
          alert("상품을 선택해주세요.");
        }else{
          axios.post('/api/product-manage/v1/storage-product',this.db_check).then(res => {
            alert("등록 완료");
          }).catch(res => {
            if(res.response.status =="401" || res.response.status =="302"){
              alert("인증 기간이 만료되었습니다.");
              this.$router.go('/');
            }else{
              this.modalMessage=res.response.data;
              this.isModalViewed=true;
            }
          })
        }
      } else{
        alert("예외처리");
      }
    },
    //빈데이터 등록
    emptyRegister : function(){
      //alert(this.productRegister.prodNm);
      axios.post('/api/product-manage/v1/empty-product', this.emptyConvert(this.productRegister)).then(res => {      
        alert("등록 완료");
      })
    },
    changeCate1 : function(event){
      axios.post('/api/category/v1/smartstore2',{
        fg : this.selectedCate1.fg,
        categoryCd : this.selectedCate1.categoryCd
      }).then(res => {
        this.cateList2 = res.data;
        this.cateList3 = null;
        this.cateList4 = null;
      })
    },
    changeCate2 : function(event){
      axios.post('/api/category/v1/smartstore3',{
        fg : this.selectedCate2.fg,
        categoryCd : this.selectedCate2.categoryCd
      }).then(res => {
        this.cateList3 = res.data;
        this.cateList4 = null;
      })
    },
    changeCate3 : function(event){
      axios.post('/api/category/v1/smartstore4',{
        fg : this.selectedCate3.fg,
        categoryCd : this.selectedCate3.categoryCd
      }).then(res => {
        this.cateList4 = res.data;
      })
    },
    // ##################################################### 보관상품 등록(개별) 메소드(currentTab = 0 ) END #############################################
    // ##################################################### 보관상품 메소드(currentTab = 2 ) START #####################################################
    storageTabClick  : function(storageProduct,idx){
      this.storageSelectedTab = idx;
      if(this.storage_check.includes(storageProduct)){
          this.storage_check.splice(this.storage_check.indexOf(storageProduct),1);
      }else{
          this.storage_check[idx] = storageProduct;
      }
    },
    storageListSend : function(index){
      axios.post('/api/product-manage/v1/storage-list',{
        pageIndex : index ? index : this.storagePagination.pageIndex,
        pageSize : this.storagePagination.pageSize
      }).then(res => {
        this.storageProductList = res.data.resultList;
        this.storagePagination.totalCount = res.data.totalCount;
        this.storagePagination.pageSize = res.data.pageSize;
        this.storagePagination.pageIndex = res.data.pageIndex;
        EventBus.$emit("storagePaginationInfo",this.storagePagination);
      }).catch(res => {
        if(res.response.status =="401" || res.response.status =="302"){
          alert("인증 기간이 만료되었습니다.");
          this.$router.go('/');
        }else{
          this.modalMessage=res.response.data;
          this.isModalViewed=true;
        }
      })
    },
    storageCheckInit : function(){
        this.storage_check=[];
    },
    storageDeleteSend : function(){
      let deleteParam = [];
      for(let data in this.storage_check){
        deleteParam.push({ prodIdx : this.storage_check[data].prodIdx });
      }
      axios.post('/api/product-manage/v1/storage-product-del',deleteParam).then(res => {
        this.storageListSend();
        alert("삭제 완료");
      }).catch(res => {
        if(res.response.status =="401" || res.response.status =="302"){
          alert("인증 기간이 만료되었습니다.");
          this.$router.go('/');
        }else{
          this.modalMessage=res.response.data;
          this.isModalViewed=true;
        }
      })
    },
    saveStore : function(){
      if(this.storage_check == null || this.storage_check == ""){
        alert("상품을 선택해주세요.");
        return;
      }

      axios.post('/api/product-manage/v1/stpProduct', this.storageConvert(this.storage_check)).then(res => {
      alert("상품이 등록되었습니다.");
      })
    }
    // ##################################################### 보관상품 메소드(currentTab = 2 ) END #####################################################

  }
}

</script>

<style scoped>
</style>



